import { useMemo } from "react"
import PropTypes from "prop-types"
import { Bar } from "react-chartjs-2"
import Card from "@mui/material/Card"
import MDBox from "../../../../components/MDBox"
import MDTypography from "../../../../components/MDTypography"
import configs from "../../../../examples/Charts/BarCharts/ReportsBarChart/configs"

function ReportsBarChart({ color, title, description, chart, data, this_day_profits }) {
  const { options } = configs(chart.labels || [], chart.datasets || {})
  const { amount } = data
  const labels = ["M", "T", "W", "T", "F", "S", "S"]
  const salesData = {
    labels,
    datasets: [
      {
        label: "Amount Profited",
        data: amount,
        backgroundColor: "rgba(255, 255, 255, 0.8)",
        borderRadius: 4,
        borderSkipped: false,
        borderWidth: 0,
        maxBarThickness: 6,
        tension: 0.4
      }
    ]
  }
  
  return (
    <Card sx={{ height: "100%" }}>
      <MDBox padding="1rem">
        {useMemo(() => {
          if (options) {
            return (
              <MDBox variant="gradient" bgColor={color} borderRadius="lg" coloredShadow={color} py={2} pr={0.5} mt={-5} height="12.5rem">
                <Bar data={salesData} options={options} />
              </MDBox>
            )
          }
        }, [color, options])}
        <MDBox pt={3} pb={1} px={1}>
          <MDBox display="flex" justifyContent="space-between">
            <MDTypography variant="h6" textTransform="capitalize">
              {title}
            </MDTypography>
            <MDBox display="flex" justifyContent="space-between" flexDirection="column">
              <MDTypography color={color} variant="h6" textTransform="capitalize">
                +{this_day_profits.toLocaleString("en-US")}
                <MDTypography pl={0.3} fontSize={"0.6rem"} variant={"caption"} fontWeight="medium" color={color}>
                  USDT
                </MDTypography>
              </MDTypography>
              <MDTypography variant="caption" textTransform="capitalize" fontSize="0.70rem">
                This day
              </MDTypography>
            </MDBox>
          </MDBox>
          <MDTypography component="div" variant="button" color="text" fontWeight="light">
            {description}
          </MDTypography>
        </MDBox>
      </MDBox>
    </Card>
  )
}

// Setting default values for the props of ReportsBarChart
ReportsBarChart.defaultProps = {
  color: "dark",
  description: ""
}

// Typechecking props for the ReportsBarChart
ReportsBarChart.propTypes = {
  color: PropTypes.oneOf(["primary", "secondary", "info", "success", "warning", "error", "dark"]),
  title: PropTypes.string.isRequired,
  description: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
  date: PropTypes.string.isRequired,
  chart: PropTypes.objectOf(PropTypes.oneOfType([PropTypes.array, PropTypes.object])).isRequired
}

export default ReportsBarChart
