import "./index.css"
import Typography from "@mui/material/Typography"
export default function Table({ columnHeaders, children }) {
  const templateColumns = Array(columnHeaders.length - 1)
    .fill("auto")
    .join(" ")
  return (
    <gridlayout is="webview" style={{ gridTemplateColumns: templateColumns + " 1fr" }}>
      {columnHeaders.map((columnHeader, index) => (
        <gridheader key={index} is="webview" className={index === 0 ? "first-col" : index === columnHeaders.length - 1 ? "last-col" : undefined}>
          <Typography
            variant="body2"
            sx={{
              display: "flex",
              color: "#61c6ef",
              lineHeight: 1.8,
              fontSize: "0.65rem",
              fontWeight: 600,
              letterSpacing: "0.03333em"
            }}>
            {columnHeader}
          </Typography>
        </gridheader>
      ))}
      {children}
    </gridlayout>
  )
}
