import { Card, Divider, Grid, Icon } from "@mui/material"
import MDBox from "../../components/MDBox"
import MDTypography from "../../components/MDTypography"
import { Link, useLocation, useNavigate } from "react-router-dom"
import profitIcon from "../../icons/profit.js"
import AlmostCompleted from "../../pages/dashboard/AlmostCompleted"
import NewOnMarket from "../../pages/dashboard/NewOnMarket"
import { supabase } from "../../supabaseClient"
import reportsBarChartData from "../../pages/dashboard/data/reportsBarChartData"
import ReportsBarChart from "../../examples/Charts/BarCharts/ReportsBarChart"
import autographIcon from "../../icons/autographIcon"
import iconNetworkGroup from "../../icons/iconNetworkGroup"
import { useQuery } from "@supabase-cache-helpers/postgrest-swr"
import LogoLoading from "../../components/Suspense/CoverLoading/LogoLoading"
import ErrorFallback from "../../components/Suspense/ErrorBoundary/ErrorFallback"
import { horizontalLogo } from "../../logos/logo"
import Leaderboard from "./Leaderboard/Leaderboard"

function DashboardPage() {
  const { pathname } = useLocation()
  const navigate = useNavigate()
  const { data: dashboard_data, error, isLoading } = useQuery(supabase.rpc("get_dashboard_page"))
  if (dashboard_data?.my_investments === 0 && dashboard_data?.my_network === 0 && pathname !== "/dashboard") navigate("/market")

  if (isLoading) return <LogoLoading />
  if (error) return <ErrorFallback error={error.message} />

  return (
    <>
      <MDBox display="flex" justifyContent="space-between">
        <MDBox width={"100%"} display="flex" justifyContent="center" py={2} ml={"-1%"}>
          {horizontalLogo}
        </MDBox>
      </MDBox>
      <MDBox pb={11} pt={2.6}>
        <Grid container spacing={2.5}>
          <Grid item xs={12} md={6} lg={3}>
            <Link to="/investments" key={1} rel="noreferrer" data-test-id="container-my-investments">
              <MDBox pb={1.5}>
                {dashboard_data && (
                  <StatisticsCard
                    icon={autographIcon}
                    title="My Investments"
                    color="info"
                    count={dashboard_data?.my_investments}
                    currency={"USDT"}
                    percentage={{
                      color: "info",
                      amount: `+${dashboard_data?.investment_increase.toLocaleString("en-US")} USDT`,
                      label: "this week"
                    }}
                  />
                )}
                {isLoading && "skeleton"}
              </MDBox>
            </Link>
          </Grid>
          <Grid item xs={12} md={6} lg={3}>
            <Link to={"/profits"} key={1} rel="noreferrer" data-test-id="container-my-profits">
              <MDBox mb={1.5} pt={0.5}>
                {dashboard_data && (
                  <StatisticsCard
                    color="success"
                    icon={profitIcon}
                    title="My Profits"
                    count={dashboard_data?.my_profits}
                    currency="USDT"
                    percentage={{
                      color: "success",
                      amount: `+${dashboard_data?.profits_increase.toLocaleString("en-US")} USDT`,
                      label: "this week"
                    }}
                  />
                )}
                {isLoading && "skeleton"}
              </MDBox>
            </Link>
          </Grid>
          <Grid item xs={12} md={6} lg={3}>
            <Link to={"/network"} key={1} rel="noreferrer" data-test-id="container-my-network">
              <MDBox mb={1.5} pt={0.3}>
                {dashboard_data && (
                  <StatisticsCard
                    color="warning"
                    icon={iconNetworkGroup}
                    title="My Network"
                    count={dashboard_data?.my_network}
                    percentage={{
                      color: "warning",
                      amount: `+${dashboard_data?.network_increase.toLocaleString("en-US")}`,
                      label: "this week"
                    }}
                  />
                )}
                {isLoading && "skeleton"}
              </MDBox>
            </Link>
          </Grid>
        </Grid>
        <Leaderboard />
        <MDBox mt={7.5} mb={1.5}>
          <Grid container spacing={3} pb={3}>
            <Grid item xs={12} md={6} lg={4}>
              <Link to={"/profits"} key={1} data-test-id="container-daily-profits" rel="noreferrer">
                <MDBox>
                  {dashboard_data && (
                    <ReportsBarChart
                      color="success"
                      title="Daily Profits"
                      date="campaign sent 2 days ago"
                      chart={reportsBarChartData}
                      amount={dashboard_data?.amount}
                      data={dashboard_data}
                      this_day_profits={dashboard_data?.this_day_profits || 0}
                    />
                  )}
                  {isLoading && "skeleton"}
                </MDBox>
              </Link>
            </Grid>
          </Grid>
        </MDBox>
        <MDBox>
          <Grid container spacing={3}>
            <AlmostCompleted almost_completed={dashboard_data?.almost_completed} />
            <NewOnMarket new_houses={dashboard_data?.new_houses} />
          </Grid>
        </MDBox>
      </MDBox>
    </>
  )
}
function StatisticsCard({ color, title, count, percentage, icon, currency }) {
  return (
    <Card>
      <MDBox display="flex" justifyContent="space-between" pt={1} px={2}>
        <MDBox
          variant="gradient"
          bgColor={color}
          color={color === "light" ? "dark" : "white"}
          coloredShadow={color}
          borderRadius="xl"
          display="flex"
          justifyContent="center"
          alignItems="center"
          width="4rem"
          height="4rem"
          mt={-2.9}>
          <Icon sx={{ fontSize: "1.8rem !important", color: "#fff" }}>{icon}</Icon>
        </MDBox>
        <MDBox textAlign="right" lineHeight={1.3}>
          <MDTypography variant="button" fontWeight="medium" color="text">
            {title}
          </MDTypography>
          <br />
          <MDTypography component={"span"} lineHeight={0} fontSize={"1.5rem"} fontWeight="medium" color={color}>
            {count.toLocaleString("en-US")}
          </MDTypography>
          <MDTypography pl={0.3} fontSize={"0.7rem"} variant={"caption"} fontWeight="medium" color={color}>
            {currency}
          </MDTypography>
        </MDBox>
      </MDBox>
      <Divider />
      <MDBox pb={2} px={2}>
        <MDTypography component="p" variant="button" color="text" display="flex">
          <MDTypography component="span" variant="button" fontWeight="bold" color={percentage.color}>
            {percentage.amount}
          </MDTypography>
          &nbsp;{percentage.label}
        </MDTypography>
      </MDBox>
    </Card>
  )
}
export default DashboardPage
