// Material Dashboard 2 React components
import MDBox from "../../../components/MDBox"
import MDTypography from "../../../components/MDTypography"
import { Card, Divider, Grid } from "@mui/material"
import DefaultInvestmentCard from "../../../examples/Cards/ProjectCards/DefaultInvestmentCard"

function AlmostCompleted({ almost_completed }) {
  return (
    almost_completed?.length > 0 && (
      <>
        <Grid item xs={12} md={6} lg={4}>
          <Card>
            <Title>Almost Completed</Title>
            <Grid sx={{ pb: 0.5, px: 2 }} container spacing={3}>
              {almost_completed?.map((house, index) => (
                <Grid item xs={12} md={6} xl={3} key={house.houseid}>
                  <DefaultInvestmentCard
                    id={house.houseid}
                    image={house.pic}
                    label={house.location}
                    title={house.title}
                    personalProgress={house.invested}
                    networkProgress={house.reached - house.invested}
                    missingProgress={house.total - house.reached}
                    value={house.total - house.reached}
                    caption="missing"
                    duration={house.period}
                    priority={index < 5 ? "high" : "auto"}
                    loading={index < 5 ? "eager" : "lazy"}
                  />
                </Grid>
              ))}
            </Grid>
          </Card>
        </Grid>
      </>
    )
  )
}
function Title({ children }) {
  return (
    <>
      <MDBox display="flex" justifyContent="start" alignItems="center" pt={2} pl={2}>
        <MDTypography variant="h5">{children}</MDTypography>
      </MDBox>
      <Divider />
    </>
  )
}
export default AlmostCompleted
