import { Card, Divider, Typography } from "@mui/material"
import React from "react"
import MDBox from "../../../components/MDBox"
import MDAvatar from "../../../components/MDAvatar"
import { useQuery } from "@supabase-cache-helpers/postgrest-swr"
import { supabase } from "../../../supabaseClient"
import LogoLoading from "../../../components/Suspense/CoverLoading/LogoLoading"
import ErrorFallback from "../../../components/Suspense/ErrorBoundary/ErrorFallback"
import goldIcon from "../../../icons/goldIcon.png"
import silver from "../../../icons/silver.png"
import bronze from "../../../icons/bronze.png"
import { Link } from "react-router-dom"
import Table from "../../../components/Table"

const Leaderboard = () => {
  const {
    data: leaderboard,
    error: leaderboardError,
    isLoading: leaderboardLoading
  } = useQuery(supabase.rpc("get_contest_leaderboard"), {
    revalidateOnFocus: false,
    revalidateOnReconnect: false
  })

  const sortedLeaderboard = leaderboard && leaderboard.slice().sort((a, b) => b.total_profits - a.total_profits)
  const positionIcons = [
    <img src={goldIcon} alt="Gold Icon" style={{ width: "50px" }} />,
    <img src={silver} alt="Silver Icon" style={{ width: "50px" }} />,
    <img src={bronze} alt="Bronze Icon" style={{ width: "50px" }} />
  ]

  if (leaderboardError) return <ErrorFallback error={leaderboardError.message} />
  if (leaderboardLoading) return <LogoLoading />
  return (
    <Card sx={{ mt: 3, overflow: "hidden", overflowY: "scroll" }} data-test-id="topNetworkers">
      <Typography variant="h6" pt={2} pb={1} sx={{ fontSize: "18px", px: 2, width: "100%" }}>
        Leaderboard
      </Typography>
      <Table columnHeaders={["POSITION", "NAME", "TOTAL PROFITS"]}>
        {leaderboard?.length > 0 &&
          sortedLeaderboard?.map((leaderboard, index) => (
            <>
              <Typography
                className="first-col"
                variant="body2"
                sx={{
                  display: "flex",
                  alignItems: "center",
                  letterSpacing: "0.03333em",
                  textTransform: "capitalize",
                  fontSize: "0.9rem",
                  color: "#7b809a",
                  fontWeight: 500
                }}>
                {index < positionIcons.length ? positionIcons[index] : <span style={{ paddingLeft: "16px" }}>{index + 1 + "Â°"}</span>}
              </Typography>
              <Link to={`/${leaderboard.username}`}>
                <MDBox
                  sx={{
                    display: "flex",
                    flexDirection: "row",
                    overflow: "visible",
                    alignItems: "center",
                    justifyContent: "start",
                    textAlign: "start",
                    gap: "15px"
                  }}>
                  <MDAvatar
                    sx={{
                      width: "36px",
                      height: "36px"
                    }}
                    alt="profile"
                    shadow="sm"
                    src={leaderboard.avatar}
                    fetchpriority={index < 8 ? "height" : "auto"}
                    loading={index < 8 ? "eager" : "lazy"}
                  />
                  <Typography
                    variant="body2"
                    sx={{
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "start",
                      textTransform: "capitalize",
                      overflow: "hidden",
                      flexWrap: "wrap",
                      letterSpacing: "0.03333em",
                      fontSize: "0.9rem",
                      color: "#7b809a",
                      fontWeight: 500
                    }}>
                    {leaderboard.username}
                  </Typography>
                </MDBox>
              </Link>
              <Typography
                className="last-col"
                variant="body2"
                sx={{
                  display: "flex",
                  alignItems: "center",
                  letterSpacing: "0.03333em",
                  textTransform: "capitalize",
                  overflow: "visible",
                  flexWrap: "wrap",
                  fontSize: "0.9rem",
                  color: "#7b809a",
                  textAlign: "center",
                  fontWeight: 500
                }}>
                {leaderboard.total_profits.toLocaleString("en-US")} USDT
              </Typography>
            </>
          ))}
      </Table>
    </Card>
  )
}

export default Leaderboard
