import Card from "@mui/material/Card"
import { Divider, Grid, Typography } from "@mui/material"
import { Box } from "@mui/system"
import SmallHouseItem from "../../../components/SmallHousesWidget"
import React from "react"
import trackEvent from "../../../components/context"

function NewOnMarket({ new_houses }) {
  return (
    new_houses && (
      <Grid item xs={12} md={6} lg={4}>
        <Card>
          <Box display="flex" justifyContent="start" alignItems="center" p={2}>
            <Typography variant="h6" sx={{ fontSize: "18px;" }}>
              New On Market
            </Typography>
          </Box>
          <Divider sx={{ mb: 0, mt: 0 }} />
          <Box p={2}>
            {new_houses.map((house, index) => (
              <React.Fragment key={house.house_id}>
                {house && (
                  <SmallHouseItem
                    name={house.name}
                    pic={house.pic}
                    address={house.address}
                    link={"/investment/" + house.house_id}
                    duration={house.duration}
                    value={house.price}
                    onClick={() => {
                      trackEvent("NewOnMarket", { investmentId: house.house_id })
                    }}
                    priority={index < 5 ? "high" : "auto"}
                    loading={index < 5 ? "eager" : "lazy"}
                  />
                )}
                {index !== new_houses?.length - 1 && <Divider />}
              </React.Fragment>
            ))}
          </Box>
        </Card>
      </Grid>
    )
  )
}

export default NewOnMarket
